'use client';

import { Button } from '@/components/ui/button';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from '@/components/ui/dialog';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { useAuth } from '@/context/AuthContext';
import { zodResolver } from '@hookform/resolvers/zod';
import { useState } from 'react';
import { Resolver, useForm } from 'react-hook-form'; // Import Resolver
import { toast } from 'sonner';
import * as z from 'zod';

// --- Form Schemas for Validation ---
const registerSchema = z.object({
  name: z.string().min(2, { message: 'Name must be at least 2 characters.' }),
  email: z.string().email({ message: 'Please enter a valid email.' }),
  password: z
    .string()
    .min(6, { message: 'Password must be at least 6 characters.' }),
});

const loginSchema = z.object({
  email: z.string().email({ message: 'Please enter a valid email.' }),
  password: z.string().min(1, { message: 'Password is required.' }),
});

// A unified type that includes ALL possible fields
type FormValues = z.infer<typeof registerSchema>;

export function AuthModal() {
  const [isLoginView, setIsLoginView] = useState(true);
  const [open, setOpen] = useState(false);
  const { login } = useAuth();

  const form = useForm<FormValues>({
    // FIX 1: Use a type assertion 'as Resolver<FormValues>' to resolve the type mismatch.
    resolver: zodResolver(
      isLoginView ? loginSchema : registerSchema
    ) as unknown as Resolver<FormValues>,
    defaultValues: {
      name: '',
      email: '',
      password: '',
    },
  });

  // FIX 2: The 'values' parameter should match the form's unified type.
  const onSubmit = async (values: FormValues) => {
    const endpoint = isLoginView ? 'login' : 'register';
    try {
      const response = await fetch(
        `http://localhost:5000/api/v1/auth/${endpoint}`,
        {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(values),
        }
      );

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || 'Something went wrong');
      }

      login(data.user, data.token);
      setOpen(false);

      toast.success(`Welcome, ${data.user.name}!`, {
        description: isLoginView
          ? "You've successfully logged in."
          : 'Your account has been created.',
      });
    } catch (error: unknown) {
      // FIX 3: Use 'unknown' for type safety
      if (error instanceof Error) {
        toast.error('Authentication Failed', {
          description: error.message,
        });
      } else {
        toast.error('Authentication Failed', {
          description: 'An unexpected error occurred.',
        });
      }
    }
  };

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        <Button>Login / Register</Button>
      </DialogTrigger>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle>
            {isLoginView ? 'Login' : 'Create an Account'}
          </DialogTitle>
          <DialogDescription>
            {isLoginView
              ? 'Welcome back! Please enter your details.'
              : 'Join us! It only takes a minute.'}
          </DialogDescription>
        </DialogHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
            {!isLoginView && (
              <FormField
                control={form.control}
                name="name" // This is now valid
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Name</FormLabel>
                    <FormControl>
                      <Input placeholder="John Doe" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            )}
            <FormField
              control={form.control}
              name="email"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Email</FormLabel>
                  <FormControl>
                    <Input placeholder="you@example.com" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="password"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Password</FormLabel>
                  <FormControl>
                    <Input type="password" placeholder="••••••••" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <Button
              type="submit"
              className="w-full"
              disabled={form.formState.isSubmitting}
            >
              {form.formState.isSubmitting
                ? 'Processing...'
                : isLoginView
                ? 'Login'
                : 'Create Account'}
            </Button>
          </form>
        </Form>
        <DialogFooter className="pt-4">
          <Button variant="link" onClick={() => setIsLoginView(!isLoginView)}>
            {isLoginView
              ? 'Need an account? Register'
              : 'Already have an account? Login'}
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
